// src/components/UserDashboard.js

import React from 'react';
import { useState } from 'react';
import logo1 from '../assets/logo1.svg';
import wallet from '../assets/wallet.svg';
import "../styles/user.css";
import { useNavigate } from 'react-router-dom';
import ShopkeeperTable from './shopkeeperTable';
import FootfallGraph from './FootfallGraph';
import FootfallTable from './FootfallTable';
import { Button } from '@mui/material';

function UserDashboard() {

  const navigate = useNavigate();
  const [showGraph, setShowGraph] = useState(false);
  const [showTable, setShowTable] = useState(false);

  const handleImageClick = () => {
    navigate('/wallet')
};
const handleGraphButtonClick = () => {
  setShowGraph(prevShowGraph => !prevShowGraph);
  setShowTable(false);  // Hide table when showing graph
};
const handleTableButtonClick = () => {
  setShowTable(prevShowTable => !prevShowTable);
  setShowGraph(false);  // Hide graph when showing table
};

  return (
    <div>
      <img src={logo1} alt="Logo" width={150} />
      <img src={wallet} alt="Wallet" class = "walletIcon" onClick={handleImageClick} />

    
    <center>
    <div className="user-dashboard">
      <h1 style = {{color:"#121858"}}>Hello user!</h1>
      <div>
            {/* ... rest of your code */}
            <ShopkeeperTable />  {/* Include the ShopkeeperTable component */}
        </div>
        <div style={{marginTop:"40px"}}>
            {/* ... other code */}
            <Button 
            halfWidth
            variant="contained"
            sx = {{borderRadius:28, color:'white', backgroundColor:"#121858",margin:'5px'}}
            onClick={handleGraphButtonClick}>
             View Footfall Graph</Button>
             <Button 
            halfWidth
            variant="contained"
            sx = {{borderRadius:28, color:'white', backgroundColor:"#121858",margin:'5px'}}
            onClick={handleTableButtonClick}>
             View Footfall Table</Button>
        </div>
          {showGraph && <FootfallGraph />}
          {showTable && <FootfallTable />}
    </div>
    </center>
    </div>
  );
}

export default UserDashboard;
